



















	export default {
		data() {
			return {
				phone:'',
				pwd:''
			};
		},
		methods: {
			login() {
				if(!(/^1[34578]\d{9}$/.test(this.phone))){ 
					uni.showToast({
						icon:'none',
						title: '手机号码输入不正确~'
					});
					return;
				} 
				if(this.pwd.length<6){
					uni.showToast({
						icon:'none',
						title:'密码不能小于六位书'
					});
					return;
				}
				uni.request({
					url: 'http://bus.liebianzhe.com/api/login/phone_login',
					method: 'POST',
					data: {
						phone:this.phone,
						password:this.pwd
					},
					success: res => {
						console.info(res);
						if(res.data.code==2){
							
							uni.showToast({
								icon:'none',
								title: '登录成功~',
							});
							uni.setStorageSync('user_info',res.data.data);
							setTimeout(function(){
								uni.switchTab({
									url:'/pages/index/index'
								})
							},2000);
							
						}else if(res.data.code==1){							
							uni.setStorageSync('user_id',res.data.data.user_id);
							uni.navigateTo({
								url:'../login/code'
							})
// 							uni.showToast({
// 								title: res.data.msg,
// 								icon:'none'
// 							});
						}else if(res.data.code==0){
							uni.showToast({
								title: '登录失败',
								icon:'none'
							});
						}
						
					},
					fail: () => {
						console.info("请求失败~");
					},
				});
			},
			code_login(){
				uni.navigateTo({
					url:'../login/code_login'
				})
			},
			forget_password(){
				uni.navigateTo({
					url:'../login/forget_password'
				})
			}
		},
	}
